export default "<template>\n    <div class=\"loader\">\n        <div class=\"slack\">\n            <div class=\"green\"></div>\n            <div class=\"blue\"></div>\n            <div class=\"orange\"></div>\n            <div class=\"pink\"></div>\n        </div>\n    </div>\n</template>\n\n<script>\n\nexport default {\n  name: 'Slack',\n};\n</script>\n\n<style lang=\"scss\" scoped>\n/* Color map */\n$default-color-white: #ffffff;\n$default-color-green: #3EB991;\n$default-color-blue: #6ECADC;\n$default-color-pink: #E01563;\n$default-color-orange: #E9A820;\n\n/* Dot */\n$dot-width: 17px;\n$dot-opacity: .75;\n\n/* Mixins */\n@mixin slack-dot-bg($background-color){\n  &::before {\n    background-color: rgba($background-color, $dot-opacity);\n  }\n}\n\n.loader {\n  height: 100%;\n  display: flex;\n  justify-content: center;\n  flex-direction: row;\n  flex-wrap: nowrap;\n}\n\n/* Slack */\n.slack {\n  width: ($dot-width * 5);\n  height: ($dot-width * 5);\n  align-self: center;\n  position: relative;\n}\n.green, .blue,\n.pink, .orange {\n  position: absolute;\n  width: $dot-width;\n  height: $dot-width;\n  border-radius: $dot-width;\n  display: block;\n  &::before {\n    right: 0;\n    /* Animation */\n    animation-name: slack-width;\n    animation-iteration-count: infinite;\n    animation-direction: alternate;\n    animation-direction: normal;\n    animation-duration: 2s;\n    width: $dot-width;\n    position: absolute;\n    height: $dot-width;\n    content: '';\n    display: block;\n    border-radius: $dot-width;\n  }\n}\n.green {\n  top: 0;\n  left: 0;\n  transform: rotate(70deg);\n  @include slack-dot-bg($default-color-green);\n}\n.blue {\n  top: 0;\n  right: 0;\n  transform: rotate(160deg);\n  @include slack-dot-bg($default-color-blue);\n}\n.orange {\n  bottom: 0;\n  right: 0;\n  transform: rotate(-110deg);\n  @include slack-dot-bg($default-color-orange);\n}\n.pink {\n  bottom: 0;\n  left: 0;\n  transform: rotate(-20deg);\n  @include slack-dot-bg($default-color-pink);\n}\n\n@keyframes slack-width {\n  0% { width: $dot-width; right: 0; }\n  30% { width: ($dot-width * 6); right: -($dot-width * 5); }\n  60% { width: $dot-width; right: -($dot-width * 5); }\n}\n</style>\n"