export default "<template>\n  <div class=\"container\">\n    <div class=\"squares\">\n      <div class=\"square\" v-for=\"i in 4\" :key=\"i\"></div>\n    </div>\n  </div>\n</template>\n\n<script>\n\nexport default {\n  name: 'Squares',\n};\n</script>\n\n<style lang=\"scss\" scoped>\n$squares-size: 40px;\n$square-size: $squares-size / 2;\n$animation: 1s linear infinite;\n\n.container {\n  text-align: center;\n}\n\n.squares {\n  position: relative;\n  display: inline-block;\n  width: $squares-size;\n  height: $squares-size;\n  overflow: hidden;\n  transform-origin: bottom left;\n  animation: squares $animation;\n}\n\n.square {\n  position: absolute;\n  width: $square-size;\n  height: 20px;\n  background-color: white;\n  left: 0px;\n  top: 0px;\n\n  &:first-child {\n    top: $square-size;\n  }\n  &:nth-child(2) {\n    left: $square-size;\n    top: $square-size;\n    animation: square2 $animation;\n  }\n  &:nth-child(3) {\n    animation: square3 $animation;\n  }\n  &:last-child {\n    left: $square-size;\n    animation: square4 $animation;\n  }\n}\n\n@keyframes squares {\n  from {\n    transform: scale(1);\n  }\n  90% {\n    transform: scale(1);\n  }\n  to {\n    transform: scale(0.5);\n  }\n}\n@keyframes square2 {\n  from {\n    transform: translateY(-50px);\n  }\n  25% {\n    transform: translate(0);\n  }\n  to {\n    transform: translate(0);\n  }\n}\n@keyframes square3 {\n  from {\n    transform: translateY(-50px);\n  }\n  50% {\n    transform: translate(0);\n  }\n  to {\n    transform: translate(0);\n  }\n}\n@keyframes square4 {\n  from {\n    transform: translateY(-50px);\n  }\n  75% {\n    transform: translate(0);\n  }\n  to {\n    transform: translate(0);\n  }\n}\n</style>\n"