export default "<template>\n  <div class=\"preloader loading\">\n    <span class=\"slice\"></span>\n    <span class=\"slice\"></span>\n    <span class=\"slice\"></span>\n    <span class=\"slice\"></span>\n    <span class=\"slice\"></span>\n    <span class=\"slice\"></span>\n  </div>\n</template>\n\n<script>\n\nexport default {\n  name: 'Hexagon',\n};\n</script>\n\n<style lang=\"scss\" scoped>\n@for $i from 1 through 6 {\n  @keyframes preload-show-#{$i} {\n    from {\n      transform: rotateZ(60 * $i + deg) rotateY(-90deg) rotateX(0deg);\n      border-left-color: #9c2f2f;\n    }\n  }\n  @keyframes preload-hide-#{$i} {\n    to {\n      transform: rotateZ(60 * $i + deg) rotateY(-90deg) rotateX(0deg);\n      border-left-color: #9c2f2f;\n    }\n  }\n\n  @keyframes preload-cycle-#{$i} {\n    $startIndex: $i * 5;\n    $reverseIndex: (80 - $i * 5);\n\n    #{$startIndex * 1%} {\n      transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0deg);\n      border-left-color: #9c2f2f;\n    }\n    #{$startIndex + 5%},\n    #{$reverseIndex * 1%} {\n      transform: rotateZ(60 * $i + deg) rotateY(0) rotateX(0deg);\n      border-left-color: #f7484e;\n    }\n\n    #{$reverseIndex + 5%},\n    100% {\n      transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0deg);\n      border-left-color: #9c2f2f;\n    }\n  }\n}\n\n@keyframes preload-flip {\n  0% {\n    transform: rotateY(0deg) rotateZ(-60deg);\n  }\n  100% {\n    transform: rotateY(360deg) rotateZ(-60deg);\n  }\n}\n\nbody {\n  background: #efefef;\n}\n\n.preloader {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  font-size: 20px;\n  display: block;\n  width: 3.75em;\n  height: 4.25em;\n  margin-left: -1.875em;\n  margin-top: -2.125em;\n  transform-origin: center center;\n  transform: rotateY(180deg) rotateZ(-60deg);\n\n  .slice {\n    border-top: 1.125em solid transparent;\n    border-right: none;\n    border-bottom: 1em solid transparent;\n    border-left: 1.875em solid #f7484e;\n    position: absolute;\n    top: 0px;\n    left: 50%;\n    transform-origin: left bottom;\n    border-radius: 3px 3px 0 0;\n  }\n\n  @for $i from 1 through 6 {\n    .slice:nth-child(#{$i}) {\n      transform: rotateZ(60 * $i + deg) rotateY(0deg) rotateX(0);\n      animation: 0.15s linear 0.9 - $i * 0.08s preload-hide-#{$i} both 1;\n    }\n  }\n\n  &.loading {\n    animation: 2s preload-flip steps(2) infinite both;\n    @for $i from 1 through 6 {\n      .slice:nth-child(#{$i}) {\n        transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0);\n        animation: 2s preload-cycle-#{$i} linear infinite both;\n      }\n    }\n  }\n}\n</style>\n"